*
  margin: 0
  padding: 0
  list-style: none
  text-decoration: none
  box-sizing: border-box
  transition: all .4s

*::-webkit-scrollbar
  width: 5px
  background-color: #fff

*::-webkit-scrollbar-thumb
  border-radius: 1px
  background-color: #394857

img
  width: 100%
  height: auto
  object-fit: cover

p, span
  font-size: 16px

html
  overflow: auto

body
  scroll-behavior: smooth !important
  box-sizing: border-box
  font-family: 'Roboto', sans-serif

.App
  background: #fff
  color: #444
  width: 100%
  height: 100%
  min-height: 100vh
  padding: 40px 0
  display: flex
  align-items: center
  justify-content: center

.fotter-line
  width: 100%
  height: 20px
  background: #f5f5f5
  border: solid 1px #ececec
  border-top: 0

.messanger
  width: 100%
  height: 550px
  display: flex
  align-items: stretch
  flex-wrap: wrap
  padding: 15px
  .left-bar, .main
    height: calc(100% - 20px)
  .left-bar
    width: 40%
    border: solid 1px #ececec
  &.mobile
    height: 100vh
    padding: 0
    .left-bar, .main
      width: 100%
    .list-action-wrap
      margin-left: auto
      .list-action
        position: relative
        background: transparent
        border: none
        display: block
        width: 30px
        height: 30px
        &::before
          display: block
          content: ''
          background: url('https://cdn1.iconfinder.com/data/icons/phone-call-3/400/Calls-18-512.png') no-repeat center center / cover
          position: absolute
          top: 0
          left: 0
          width: 100%
          height: 100%
          transform: scale(1.1)
          overflow: hidden
          border-radius: 50%




  .main
    width: 60%
    border: solid 1px #ececec
    border-left: 0
  .head
    align-items: center
    justify-content: space-between
    border-bottom: solid 1px #ececec
  .chat-list-holder
    padding: 10px
    height: 85%
    // height: calc(85% - 20px)
    .chat-list
      overflow-y: scroll
      height: calc(100% - 43px)

  .chat-list-title
    padding: 5px 0 10px 0

  .chat-list-item
    display: flex
    flex-wrap: wrap
    align-items: center
    justify-content: space-between
    border-bottom: 1px solid #ececec
    padding: 10px 5px
    &:hover
      background: #f5f5f5
  .avatar
    width: 35px
    height: 35px
    border-radius: 50%
    overflow: hidden
    margin-right: 8px
  .contact-chat-message
    width: 75%
    display: flex
    align-items: center
    >span
      width: calc(100% - 38px)
      position: relative
    .message-ico
      width: 15px
      height: auto
      position: absolute
      top: 0
      right: 10px

  .message-text
    width: auto
    display: -webkit-box
    -webkit-line-clamp: 1
    -webkit-box-orient: vertical
    overflow-y: hidden
  .message-time
    width: auto
    max-width: 25%
    padding: 5px
    overflow: hidden

  .contact-name
    width: auto

  .head, .active-chat-head
    background: #f5f5f5
    padding: 10px
    display: flex
    height: 15%
  .user
    display: flex
    align-items: center
    .logout
      padding: 5px
      background: transparent
      border: solid #394857 1px
      border-radius: 10px
      &:hover
        background: #394857
        color: #ececec

  .livesearch
    width: 60%

  .active-chat-head
    border-bottom: solid 1px #ececec
    height: 15%
    align-items: center
  .active-chat-body
    padding: 10px 25px 10px 10px
    overflow: overlay
    height: 69%

  .contact-message
    display: flex
    align-items: center
    // flex-direction: column
    margin-bottom: 20px

  .contact-message
    justify-content: start
    align-items: flex-start

  .my-message
    margin-bottom: 20px
    display: flex
    flex-direction: column
    align-items: flex-end
  .contact-message-text
    background: #394857
    color: #ececec

  .my-message-text
    background: #e1e1e1
    color: #444

  .inner
    max-width: calc( 100% - 43px )

  .my-message-text, .contact-message-text
    padding: 10px 15px
    border-radius: 30px
    position: relative

  .contact-message-time, .my-message-time

    color: #444
    padding: 5px
    font-size: 10px

  .new-message-sender
    height: 16%
    padding: 20px
    background: #f5f5f5
    border-top: solid 1px #ececec
  form
    position: relative
    overflow: hidden
  input[type='text'], textarea
    outline: none
    border-radius: 30px
    border: none
    width: 100%
    height: 100%
    padding: 10px
  textarea
    padding-right: 10%
    overflow: hidden
    resize: both

  input[type='submit']
    border: none
    position: absolute
    width: 20px
    height: 20px
    right: 10px
    top: 50%
    transform: translateY(-50%)
    display: block
    background: url('https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcRjOx_qeYwh2KIAKsx7ggAQVT2-A1oEn7TmJn4_6u4Fb86IxNPSpqb-iEbGf-C0kEennoE&usqp=CAU') no-repeat center center / contain
    border-radius: 50%
    overflow: hidden
    padding: 2px
    

  .message-time
    span
      font-size: 10px

  .login-screen
    width: 100%
    height: 100%
    display: flex
    align-items: center
    justify-content: center
    flex-direction: column
    background: url('https://itech.co.ua/wp-content/uploads/2022/01/telegram.png') no-repeat center center / cover
    .login-screen-title
      font-size: 30px
      padding: 20px
      color: #00207e
    button
      padding: 10px
      border-radius: 10px
      color: #00207e
      font-size: 17px
      border: none
      background: #29a7e6

// @media screen and (min-width: 567px)

@media screen and (max-width: 768px)

  .App
    height: auto
    min-height: 100vh
    padding: 0
    display: block